.btn {
  display: inline-block;
  min-width: 200px;
  border-radius: 16px;
  color: #000000;
  text-align: center;
  line-height: 20px;
  padding: 18px 24px;
  transition: all 0.3s ease;
  border: 2px solid;
}

.btn:hover {
  transition: all 0.3s ease;
}

.btn.primary-btn {
  background-color: #e7fafe;
  border-color: #e7fafe;
}

.btn.primary-btn:hover {
  background-color: transparent;
  color: #000000;
}

.btn.primary-btn.outline-btn {
  background-color: transparent;
  color: #e7fafe;
}

.btn.primary-btn.outline-btn:hover {
  background-color: #e7fafe;
  color: #000000;
}

.btn.secondary-btn {
  background-color: #ff6363;
  border-color: #ff6363;
}

.btn.secondary-btn:hover {
  background-color: transparent;
  color: #ff6363;
}

.btn.secondary-btn.outline-btn {
  background-color: transparent;
  color: #ff6363;
}

.btn.secondary-btn.outline-btn:hover {
  background-color: #ff6363;
  color: #ffffff;
}

.btn.dark-btn {
  background-color: #000000;
  border-color: #000000;
  color: #ffffff;
}

.btn.dark-btn:hover {
  background-color: transparent;
  color: #000000;
}

.btn.dark-btn.outline-btn {
  background-color: transparent;
  color: #000000;
}

.btn.dark-btn.outline-btn:hover {
  background-color: #000000;
  color: #ffffff;
}

.btn.inst-btn {
  display: flex;
  align-items: center;
}

.btn.inst-btn i {
  font-size: 22px;
  margin-left: 16px;
}

.btn.btn-search {
  min-width: 0;
  padding: 10px;
}

@media (min-width: 768px) {
  .btn.btn-search {
    min-width: 160px;
    padding: 22px 46px;
  }
}

.btn.btn-search:hover {
  background-color: rgba(0, 0, 0, 0.8);
  color: #ffffff;
}

.search {
  position: relative;
}

.search__input {
  width: 100%;
  padding: 20px 10px 20px 20px;
  border-radius: 24px;
  background-color: #ffffff;
}

@media (min-width: 768px) {
  .search__input {
    padding: 31px 10px 31px 32px;
  }
}

.search__btn {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}

.search.border {
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 24px;
}

.recipe-card {
  display: flex;
  flex-direction: column;
  background: linear-gradient(180deg, rgba(231, 249, 253, 0) 0%, #e7f9fd 100%);
  border-radius: 30px;
  padding: 16px 16px 32px 16px;
  width: 100%;
}

.recipe-card__img {
  position: relative;
  margin-bottom: 24px;
}

.recipe-card__like {
  background-color: #ffffff;
  color: #dbe2e5;
  border-radius: 50%;
  height: 48px;
  width: 48px;
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 1;
  font-size: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.recipe-card__like:hover {
  color: #ff6363;
}

.recipe-card__like.active {
  color: #ff6363;
}

.recipe-card__title {
  font-weight: 600;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.04em;
  margin-bottom: 24px;
}

.recipe-card__mark {
  display: flex;
  align-items: center;
  gap: 24px;
}

.recipe-card__label {
  display: flex;
  align-items: center;
}

.recipe-card__label i {
  font-size: 24px;
}

.recipe-card__label span {
  color: rgba(0, 0, 0, 0.6);
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: -0.02em;
  padding-left: 10px;
}

.recipe-card.recipe-card--sm {
  background: #ffffff;
  padding: 0;
}

.recipe-card.recipe-card--sm .recipe-card__img {
  margin-bottom: 16px;
}

.recipe-card.recipe-card--sm .recipe-card__title {
  font-size: 18px;
  line-height: 26px;
}

.foodieland-card {
  display: flex;
  flex-direction: column;
  padding: 40px 16px 32px 16px;
  background: url(../img/healthyfood.png), linear-gradient(180deg, #357355 0%, #1d483b 100%);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  min-height: 434px;
}

@media (min-width: 768px) {
  .foodieland-card {
    min-height: 0;
  }
}

.foodieland-card__title {
  font-size: 24px;
  line-height: 30px;
  color: #ffffff;
  text-align: center;
  font-style: italic;
  max-width: 180px;
  margin: 0 auto;
}

.foodieland-card__link {
  text-align: center;
  margin-top: auto;
}

.foodieland-card__link a {
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: -0.02em;
  color: rgba(255, 255, 255, 0.6);
}

.foodieland-card.foodieland-card--lg {
  height: 343px;
}

.mini-card {
  display: flex;
  flex-direction: column;
  background: linear-gradient(180deg, rgba(112, 130, 70, 0) 0%, rgba(112, 130, 70, 0.1) 100%);
  border-radius: 30px;
  padding: 100px 50px 30px;
  align-items: center;
  text-align: center;
  width: 100%;
  margin-bottom: 80px;
  position: relative;
  cursor: pointer;
}

@media (min-width: 1200px) {
  .mini-card {
    margin-bottom: 0;
  }
}

.mini-card__img {
  height: 100px;
  width: 100px;
  position: absolute;
  bottom: 70%;
}

.mini-card__name {
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  letter-spacing: -0.02em;
}

.mini-card.green {
  background: linear-gradient(180deg, rgba(108, 198, 63, 0) 0%, rgba(108, 198, 63, 0.1) 100%);
}

.mini-card.red {
  background: linear-gradient(180deg, rgba(204, 38, 27, 0) 0%, rgba(204, 38, 27, 0.1) 100%);
}

.mini-card.orange {
  background: linear-gradient(180deg, rgba(240, 158, 0, 0) 0%, rgba(240, 158, 0, 0.1) 100%);
}

.mini-card.gray {
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.05) 100%);
}

.checkbox-btn {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  border: 2px solid #dbe2e5;
  border-radius: 50%;
  background-color: #ffffff;
}

.checkbox-btn i {
  font-size: 14px;
  opacity: 0;
  transition: all 0.3s ease;
}

.checkbox-btn i.active {
  opacity: 1;
  color: #000000;
}

.checkbox-text {
  display: inline-block;
  padding-left: 24px;
  line-height: 22px;
  letter-spacing: -0.02em;
}

.checkbox-text.active {
  text-decoration: line-through;
  color: rgba(0, 0, 0, 0.6);
  opacity: 0.2;
}

.checkbox input:checked + .checkbox-btn i {
  opacity: 1;
  color: #000000;
}

.hidden {
  display: none;
}

